.container {
    display: flex;
    min-height: 80%;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
    z-index: -1;
  }
  
  @media (min-width: 1100px) {
    .container {
      padding-left: 100px;
      padding-right: 100px;
      width: 1100px;
    }
  }
  
.gameCard {
    width: 150px;
    height: 150px;
    margin-right: 20px;
    margin-bottom: 20px;
    background-size: cover;
    box-shadow: 5px 5px 5px darkslategrey;
    border-radius: 5px;
}

.gameCard:hover {
    border: 2px solid darkslategrey;
}

@media (max-width: 750px) {

    .gameCard {
        height: 100px;
        width: 100px;
    }
}

@media (max-width: 400px) {

    .gameCard {
        height: 75px;
        width: 75px;
    }
}
.jumbo {
    margin-top: 80px; 
}

.jumbotron {
    box-shadow: 0 2px 5px 0 rgb(22,31,42);
    color: rgb(251,242,247)
}

.instructions {
    text-align: center;
    font-size: 2em;
}

.header {
    background: rgb(92,63,111);
    width: 100%;
    min-height: 85px;
    color: ivory;
    position: fixed;
    top: 0;
    margin: 0 auto;
    box-shadow: 0 5px 5px rgb(22,31,42);
    z-index: 1000;
}

.header ul {
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: center;
}

.header li {
    display: inline-block;
    width: 33%;
    padding-top: 20px;
}

#score {
    font-size: 1.2em;
}

@media (max-width: 700px) {

    .header li {
        display: block;
        text-align: center;
        width: 100%;
        padding-top: 0;
    } 
}

.gameMessage {
    font-size: 1.5em;
}

.black {
    color: ivory;
}

.correct {
    color: LAWNGREEN;
}

.incorrect {
    color: ivory;
}
